.github_contribution{
  color: #ffff;
  font-weight: 800;
  font-family: "Lobster Two", cursive;
  position: relative;

  text-transform: uppercase;
  font-size: 2.6em;
  letter-spacing: 4px;
  overflow: hidden;
  background: linear-gradient(90deg, rgb(164, 133, 255), orange, #000);
  background-repeat: no-repeat;
  background-size: 80%;
  /* animation: animate 3s linear infinite; */
  -webkit-background-clip: text;
  -webkit-text-fill-color: rgba(255, 255, 255, 0);

  background-color: orange;
  /* background-image: radial-gradient(
      at 40% 20%,
      hsla(82, 100%, 74%, 1) 0px,
      transparent 50%
    ),
    radial-gradient(at 80% 0%, hsla(243, 100%, 56%, 1) 0px, transparent 50%),
    radial-gradient(at 0% 50%, hsla(49, 100%, 93%, 1) 0px, transparent 50%),
    radial-gradient(at 80% 50%, hsla(34, 100%, 76%, 1) 0px, transparent 50%),
    radial-gradient(at 0% 100%, hsla(76, 100%, 77%, 1) 0px, transparent 50%),
    radial-gradient(at 80% 100%, hsla(296, 100%, 70%, 1) 0px, transparent 50%),
    radial-gradient(at 0% 0%, hsla(37, 100%, 76%, 1) 0px, transparent 50%); */
}
.Image_Section{
  width: 70%;
  margin: auto;
  border: 1px solid darkblue;
}


.githubImages{
  position: relative;
  z-index: 1;
  margin:auto;
}

@media only screen and (max-width: 780px) and (min-width: 320px) {

  .github_contribution{
    font-size: 28px;
  }


}